.gallery {
  color: $secondary-text-color;
}

.gallery__item {
  display: grid;
  justify-items: center;
  align-items: center;
}

.gallery__img-wrapper {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.gallery__img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

.gallery__heading {
  z-index: 2;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  max-width: 440px;
  padding: 30px;
  margin: 28px 0 0;
  text-align: center;
  font-family: $secondary-font-family;
  font-size: 26px;
  line-height: 26px;
  font-weight: 400;
  letter-spacing: 0.05em;

  @media (min-width: $tablet-width) {
    max-width: none;
    margin-top: 7px;
  }

  @media (min-width: $laptop-width) {
    margin-top: 0;
    margin-bottom: 4px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 8px;
    margin-bottom: 0;
  }

  @media (min-width: $wide-desktop-2-width) {
    font-size: 72px;
    line-height: 72px;
  }
}

.gallery .gallery__button {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.gallery .gallery__pagination {
  bottom: 16px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}
